import React, { Component } from 'react';
import { Route, Routes, Link} from 'react-router-dom';
import Main from './components/pages'
import Ledger from "./microworld/XRPLedger"
import AuthPage from './components/auth';
import RabbitRun from './microworld/RabbitRun/Cover'
import Maintenance from './components/pages/maintenance';
import NotFound from './components/pages/notFound';

class Routers extends Component {
    constructor(props) {
        super(props);

        this.state = {
          address: "",
          ledger: new Ledger(),
          maintenance: process.env.REACT_APP_UNDER_MAINTENANCE === 'true'
        };
    }

    componentDidUpdate(prevProps, prevState) {
        console.log(this.state, prevState, prevProps);
    }

    render() {
        return (
            <Routes>
                <Route exact path='/' element={
                    <Main 
                        ledger={this.state.ledger} 
                        handleAddress={id => this.setState({ address: id })}
                        add={this.state.address} 
                    />
                }/>
                <Route exact path='/Auth' element={
                    <AuthPage 
                        ledger={this.state.ledger}
                    />
                }/>
                <Route exact path='/RabbitRun' element={ 
                    <RabbitRun 
                        ledger={this.state.ledger}
                    />
                }/>
                <Route exact path='/Maintenance' element={ 
                    <Maintenance />
                }/>
                 <Route exact path='*' element={ 
                    <NotFound />
                }/>
            </Routes>
        )
    }
}

export default Routers;