import React, { useEffect, useRef, useState, Component } from "react";
import { Container, Button } from "react-bootstrap";
import { useNavigate  } from "react-router-dom";
import Game from "./Game";


const RabbitRun = (props) => {
  const gameContainer = useRef(null);
  const navigation = useNavigate();
  const [showCover, setShowcover] = useState(true);
  const [showGameContent, setShowGameContent] = useState(false);


  const isValidAddress = () => {
    if (localStorage.xrpAddress) {
        return props.ledger.isValidAddress(localStorage.xrpAddress);
    }

    return false;
  }

  const redirectToAuth = () => {
    localStorage.setItem('href', "/RabbitRun");
    navigation('/Auth', { state: { name:'Xyz' }});
  }

  useEffect(() => {
    if (!isValidAddress()) {
      redirectToAuth();
    }
  }, []);

  return (
    <>
      <div className="App-header" ref={gameContainer}>
        { showCover &&
          <StartCover
            show={showCover}
            onHide={() => {
              setShowcover(false)
              setShowGameContent(true)
            }}
          />
        }

        {showGameContent && !showCover &&
          <GameInitiator
            cont={gameContainer}
          />
        }
      </div>
    </>
  );
}


const StartCover = (props) => {
  const onClick = () => {
    props.onHide();
  }

  return(
    <>
        <header className="masthead">
            <Container>
                <div className="masthead-heading">MicroWorld Games!</div>
                <div className="masthead-subheading text-uppercase">Welcome to Rabbit Run Game</div>
                <Button onClick={onClick} className="btn btn-primary btn-xl text-uppercase">Click to Start</Button>
            </Container>
        </header>
    </>
  );
}

class GameInitiator extends Component {
  constructor(props) {
      super(props);
      this.gameContainer = React.createRef();
      this.gameScore = React.createRef();
      this.gameOver = React.createRef();
  }

  componentDidUpdate(prevProps) {
    if ((this.webviewer && prevProps.envConfig.envVar !== this.props.envConfig.envVar)) {
      this.webviewer.updateEnvConfig(this.props.envConfig);
    }
  }

  componentDidMount() {
     this.app = new Game(this.gameContainer, this.gameScore, this.gameOver,  this.score);

     if (this.app.gameOver) {
       console.log('endddddddddddddddddd', this.app.score)
     }
  }

  componentWillUnmount() {
    this.app.destroy();
  }

  sendPayment = () => {

  }

  render() {
      return(
          <>
              <div id="world" ref={this.gameContainer}></div>
              <div className='gameoverInstructions hidden' ref={this.gameOver}>
                  Game Over
                  <div>
                    <Button variant="outline-warning" onClick={() => this.sendPayment()}>Collect tokens</Button>
                  </div>
              </div>
              <div className="dist">
                  <div className="label">distance { this.score}</div>
                  <div className="distValue" ref={this.gameScore}>000</div>
              </div>
              <div className="instructions">
                  Click to JUMP
                  <span className="lightInstructions">
                   Grab the carrots and avoid the hedgehogs
                  </span>
              </div>
          </>
      );
  
  }
};
export default RabbitRun;