import React from "react";
import {
  Container,
  Row
} from 'react-bootstrap';

const NotFound = () => {
  return (
    <section className="maintenance vh-100">
      <Container className="h-custom" >
        <Row className="d-flex justify-content-center align-items-center h-100">
            <div className="col-md-12">
                <div className="error-template">
                    <h1>Oops!</h1>
                    <h1>Page Not Found <small><font face="Tahoma" color="red">Error 404</font></small></h1>
                    <div>
                      <p>
                        The page you requested could not be found, either contact your webmaster or try again. 
                      </p>
                      <p>
                        Use your browsers <b>Back</b> button to navigate to the page you have prevously come from
                      </p>
                      <p className="fs-4">
                         Micro<span color="red">World</span> Team
                      </p>
                    </div>
                </div>
            </div>
        </Row>
      </Container>
    </section>
  );
}

export default NotFound;