import React, {useState, useEffect, Component} from "react";
import {
    Nav, 
    Navbar, 
    NavDropdown, 
    Badge, 
    Container, 
    Modal,
    Button,
    ToastContainer,
    Toast,
    Form,
    Col
} from 'react-bootstrap'

const Alert = (props) => {
    return (
        <ToastContainer position="top-end" className="p-3" >
            <Toast 
            {...props}
            onClose={props.onHide} 
            autohide>
                <Toast.Header>
                <img
                    src="holder.js/20x20?text=%20"
                    className="rounded me-2"
                    alt=""
                />
                <strong className="me-auto">Notification!</strong>
                </Toast.Header>
                <Toast.Body>{props.msg}</Toast.Body>
            </Toast>
      </ToastContainer>
    );
}

const Header = (props) => {
    const [ledgerInstance, setLedger] = useState(props.events.ledger);
    const [modalShow, setModalShow] = useState(false);
    const [submitted, setSubmitted] = useState(false);
    const [alertShow, setAlertShow] = useState(false);
    const [textAddress, setTextAddress] = useState(null);
    const [buttonHide, setButtonHide] = useState(null);
    const [xrpAddress, setXrpAddress] = useState("");
    const [hideGames, setHideGame] = useState(null);
    const [errorMsg, setErrorMsg] = useState("");
    const [errorColor, setErrorColor] = useState("info")

    const reset = () => {
        setModalShow(true);
        setSubmitted(false);
    }

    const showErrorAlert = () => {
        setAlertShow(true);
        setErrorColor("danger");
        setErrorMsg( "Address `" + xrpAddress+ "` is not valid");
    }

    const checkAddress = async () => {
        const isValid = ledgerInstance.isValidAddress(xrpAddress);
        if (isValid) {
            const response = await ledgerInstance.getAccountLines(xrpAddress);
            if (response[0] && response[0].result.lines) {
                for (let i = 0; i < response[0].result.lines.length; i++) {
                    const item = response[0].result.lines[i];
                    if (item.account && item.account === process.env.REACT_APP_WALLET_ISSUER_ADDRESS) {
                        return [true, true];
                    }
                }
            }
        }

        return [isValid, false];
    }

    useEffect(async() => {
        if (submitted) {
            if (xrpAddress !== "") {
                const isValid = await checkAddress();
                if (isValid[0]) {
                        const settings = (
                            <NavDropdown title="Settings" className="navLink">
                                <NavDropdown.Item>
                                    <span className="d-lg-inline">{xrpAddress}</span>
                                </NavDropdown.Item>
                                <NavDropdown.Item>
                                    <Button variant="primary" onClick={() => reset()}>Change XRP Address</Button>
                                </NavDropdown.Item>
                            </NavDropdown>
                        );
                        setAlertShow(true);
                        setErrorColor("info");
                        setErrorMsg( "Address is Valid!"); 

                        setTextAddress(settings);
                        setButtonHide(null);
                        setHideGame("navLink");

                        localStorage.setItem('xrpAddress', xrpAddress);
                } else {
                    showErrorAlert();   
                }

                setTimeout(() => {
                    if (isValid[1]) {
                        setAlertShow(true);
                        setErrorColor("info");
                        setErrorMsg( "Trustlines are Set!");  
                    } else {
                        setAlertShow(true);
                        setErrorColor("warning");
                        setErrorMsg( "Trustline need to be Set before playing!"); 
                    }
                }, 4000)
            } else {
                showErrorAlert(); 
            }
        }

        if (!submitted && xrpAddress === "") {
            const signUp = (
                <Button variant="primary" onClick={() => reset()}>
                    Sign in
                </Button>
            );
            setButtonHide(signUp);
            setTextAddress(null);
            setHideGame("navLink disabled")
        }


    }, [submitted]);    

    return (
        <>
         <Navbar bg="light" variant="light" expand="lg" fixed="top" id="nav">
            <Container className="px-lg-3">
                <Navbar.Brand className="navName" href="#!">Micro<span>World</span></Navbar.Brand>
                <Navbar.Toggle aria-controls="navbarSupportedContent" />
                <Navbar.Collapse id="navbarSupportedContent">
                    <Nav className="ms-auto mb-2 mb-lg-0">
                        <Nav.Link className="navLink" href="#home">Home</Nav.Link>
                        <Nav.Link className="navLink" href="#about">About</Nav.Link>
                        <Nav.Link className="navLink" href="#services">Services</Nav.Link>
                        <Nav.Link className="navLink" href="#contact">Contact</Nav.Link>
                        <NavDropdown className="navLink" title="Others" id="basic-nav-dropdown">
                            <NavDropdown.Item className="navLink" disabled href="#action/3.2">Whitepaper <Badge bg="secondary">Soon</Badge></NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item className={hideGames} href="/RabbitRun">Rabbit Run <Badge bg="secondary">Development</Badge></NavDropdown.Item>
                            <NavDropdown.Item className={hideGames} href="#">2D - 2 <Badge bg="secondary">Soon</Badge></NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item className={hideGames} href="#">3D - 1 <Badge bg="secondary">Soon</Badge></NavDropdown.Item>
                            <NavDropdown.Item className={hideGames} href="#">3D - 2 <Badge bg="secondary">Soon</Badge></NavDropdown.Item>
                        </NavDropdown>
                        {textAddress}
                    </Nav>
                </Navbar.Collapse>
                {buttonHide}
                <Button className="mx-1" variant="success" 
                onClick={()=> window.open("https://gleam.io/0MJQU/micro-world-airdrop-listing", "_blank")}>
                    Airdrop Listing
                </Button>
                {modalShow &&
                (<ModalForm
                    show={modalShow}
                    onHide={() => {
                        setSubmitted(true);
                        setModalShow(false);
                    }}
                    handleSubmit={id => setXrpAddress(id)}
                />) }
                {alertShow && (<Alert
                    show={alertShow}
                    bg={errorColor}
                    onHide={()=> setAlertShow(false)}
                    delay={4000}
                    msg={errorMsg}
                />)}
            </Container>
        </Navbar>
        </>
    );
}


class ModalForm extends Component {

    state={ address: null }
  
    handleChange = (e) => this.setState({address: e.target.value})
    handleSubmit = () => {
        this.props.handleSubmit(this.state.address);
        this.props.onHide();
    }
  
    render(){
      return(
        <Modal 
            {...this.props}
            size="md"
            aria-labelledby="contained-modal-title-vcenter"
            backdrop="static"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>MicroWorld</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form.Group >
                    <Form.Label>XRP Address: </Form.Label>
                    <Form.Control type="text" onChange={this.handleChange} placeholder="Enter XRP Address"/>           
                </Form.Group>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="primary" type="submit" onClick={this.handleSubmit}>
                    Submit
                </Button>
            </Modal.Footer>
        </Modal>
      )
    }
}


export default Header;